.thankyou {position: relative;padding: 50px 0 90px;min-height: calc(100vh - 346px);display: flex;align-items: center;}.container {max-width: 936px;margin: 0 auto;padding: 0 15px;}.thankyou img {max-width: 304px;margin: 0 auto;display: block;}.thankyou h2 {font-family: 'DM Sans', sans-serif !important;font-weight: 500;font-size: 32px;line-height: 120%;text-align: center;letter-spacing: -0.03em;text-transform: capitalize;color: #252d43;padding-bottom: 20px;padding-top: 10px;margin: 0;}.thankyou p {font-family: 'Inter', sans-serif;font-weight: 400;font-size: 16px;line-height: 140%;text-align: center;letter-spacing: -0.03em;color: #252d43;max-width: 636px;margin: 0 auto;}@media screen and (max-width: 991px) {.thankyou {padding: 68px 0;min-height: calc(100vh - 370px);}.thankyou h2 {font-size: 33px;}}